<template>
  <div class="goods">
      <options :data="dataOption" v-on:change="optchange" ref="goodsOpt"/>
      <tables :data="dataTable" :list="tableList" v-on:change="tabchange"/>
      <div class="demonstration-box">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="pageParams.size"
          layout="total, prev, pager, next"
          :total="pageParams.totalCount"
        ></el-pagination>
      </div>
      <el-dialog
        title="提示"
        :visible.sync="dialogVisible"
        width="30%">
        <span>{{dialogContent}}</span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="sure">确 定</el-button>
        </span>
      </el-dialog>
  </div>
</template>

<script>
import Options from 'views/common/options'
import Tables from 'views/common/tables'
import queryString from "querystring"
import page from 'data/page'

const dataOption = {
  leftMenu: [
    { id:'1', name:'公告标题：', placeholder:'请输入公告标题', module:'name'},
    { id:'2', name:'公告时间：', placeholder:'请选择公告时间', module:'beginTime', type:'date'},
  ],
  rightMenu: [
    { id:'r1', name:'查询'},
    { id:'r2', name:'新增'}
  ]
}

const dataTable = {
  table: [
    { label:'公告标题', prop:'name'},
    { label:'公告时间', prop:'count'},
  ],
  edit: [
    {name: '编辑', id: 't1'},
    {name: '删除', id: 't2'},
  ]
}

const tips = ['确认删除该条信息?'];

export default {
  name: 'notices',
  mixins: [page],
  data() {
    return {
      dataOption: dataOption,
      dataTable: dataTable,
      dialogVisible: false,
      dialogContent: tips[0],
      tableList: [],
      loading: false,
      currentPage: 1,
      id: ''
    }
  },
  components: {
    Options,
    Tables
  },
  mounted(){
    this.search();
  },
  methods:{
    optchange(params){
      if(params.id === 'r1') return this.search();
      if(params.id === 'r2') return this.$router.push(`noticesAdd?type=new`);
    },
    tabchange(params){
      this.id = params.data.id;
      // 编辑
      if(params.id === 't1') return this.$router.push(`noticesAdd?type=edit&id=${params.id}`);
      // 删除
      if(params.id === 't2') return this.dialogVisible = true;
    },
    sure(){
      this.search();
      this.dialogVisible = false;
    },
    search(){  //搜索
      const optParams = this.$refs.goodsOpt.getData();
      const pageParams = { size: 20, page: this.currentPage -1 };

      const params = Object.assign({}, optParams, pageParams)
      this.tableList = [
        {name: '张三1', count: '100', price: '1200', time: '2018-01-10--2015-02-10'},
        {name: '张三2', count: '100', price: '1200', time: '2018-01-10--2015-02-10'},
        {name: '张三3', count: '100', price: '1200', time: '2018-01-10--2015-02-10'},
      ]
      // this.loading = true;
      // const param = queryString.stringify(params || {});
      // let url = urls[code];
      // this.api.get(`/role`).then((res)=>{
        // this.loading = false;
        // this.data = res.data.list;
        // this.setPage(res.data)
        // this.dataInit();
      // }).catch((err) => {
      //   console.log(err)
      //   this.$message(err.message)
      // })
    },
    dataInit(){//数据初始化

    }
  }
}
</script>
 
